<template>
  <div class="reveal">
    <div class="circle circle1">
      1
      <div style="color:#f76c1a">填写报名信息</div>
    </div>
    <div class="line line1"></div>
    <div class="line" :class="(this.$store.state.flagTwo==true||this.$store.state.flagThree==true)?'line1':'line'"></div>

    <div :class="(this.$store.state.flagTwo==true||this.$store.state.flagThree==true)?'bgc2':'circle'">
      2
      <div style="width: 100px;margin: 4px 0 0 -34px" :class="(this.$store.state.flagTwo==true||this.$store.state.flagThree==true)?'color2':'color'">提交参赛课件</div>
    </div>
    <div class="line" :class="(this.$store.state.flagTwo==true||this.$store.state.flagThree==true)?'line1':'line'"></div>
    <div class="line" :class="(this.$store.state.flagThree==true)?'line1':'line'"></div>

    <div :class="(this.$store.state.flagThree==true)?'bgc2':'circle'">
      3
      <div :class="(this.$store.state.flagThree==true)?'color2':'color'">完成</div>
    </div>
  </div>
</template>

<script>
  import store from '../vuex/store'

  export default {
    name: "message",
    data() {
      return {}
    },
    store
  }
</script>

<style scoped lang="scss">
  .reveal {
    display: flex;
    margin-top: 30px;
    .circle {
      width: 30px;
      height: 30px;
      color: #fff;
      line-height: 30px;
      text-align: center;
      border-radius: 50%;
      border: 5px solid #f3f3f3;
      position: relative;
      background-color: #d1d1d1;
      div {
        width: 100px;
        color: #d1d1d1;
        margin-top: 5px;
        margin-left: -35px;
      }
    }
    .circle1 {
      background-color: #f76c1a;
      border-color: #fdd3ba;
    }
    .line {
      width: 80px;
      height: 2px;
      margin-top: 18px;
      background-color: #f3f3f3;
    }
    .line1 {
      width: 80px;
      height: 2px;
      margin-top: 18px;
      background-color: #fdd3ba;
    }
  }

  .bgc2 {
    width: 30px;
    height: 30px;
    color: #fff;
    line-height: 30px;
    text-align: center;
    border-radius: 50%;
    border: 5px solid #fdd3ba;
    position: relative;
    background-color: #f76c1a;
  }

  .borderLine {
    background-color: #fdd3ba;
  }
  .color{
    color: #ccc
  }
  .color2{
    color:#f76c1a
  }
</style>



