<template>
    <div class="preliminary">
       <span>我的初赛</span>
       <message></message>

       <el-form v-show="this.$store.state.flagOne" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="姓名" prop="name" style="width: 450px">
          <el-input v-model="ruleForm.name" size="mini" placeholder="请输入姓名"></el-input>
        </el-form-item>


         <el-form-item label="性别" prop="sex">
           <!--<el-radio-group v-model="ruleForm.sex">-->
             <!--<el-radio label="男"></el-radio>-->
             <!--<el-radio label="女"></el-radio>-->
           <!--</el-radio-group>-->

           <el-radio-group v-model="ruleForm.sex">
             <el-radio label="男"></el-radio>
             <el-radio label="女"></el-radio>
           </el-radio-group>

         </el-form-item>


        <el-form-item label="出生日期" prop="region" >
            <el-date-picker
              style="width: 350px"
              size="mini"
              v-model="ruleForm.region"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
        </el-form-item>


        <el-form-item label="手机号码" prop="call" style="width: 450px">
          <el-input v-model="ruleForm.call" size="mini" placeholder="请输入电话号码"></el-input>
        </el-form-item>


        <el-form-item label="职称" prop="professional" style="width: 450px">
          <el-input v-model="ruleForm.professional" size="mini" placeholder="请输入职称"></el-input>
        </el-form-item>


        <el-form-item label="授课年级" prop="grade" style="width: 450px">
          <el-input v-model="ruleForm.grade" size="mini" placeholder="请输入年级"></el-input>
        </el-form-item>


        <el-form-item label="任职学校" prop="school" style="width: 450px">
          <el-input v-model="ruleForm.school" size="mini" placeholder="请输入任职学校"></el-input>
        </el-form-item>


        <el-form-item label="联系地址" prop="address" style="width: 450px">
          <el-input v-model="ruleForm.address" size="mini" placeholder="请输入联系地址"></el-input>
        </el-form-item>


         <el-form-item label="参赛性质" prop="nature">
           <el-radio-group v-model="ruleForm.nature" prop="nature1" @change="change">
             <el-radio label="个人"></el-radio>
             <el-radio label="团体"></el-radio>
           </el-radio-group>
         </el-form-item>


         <el-form-item label="团队名称" prop="nature1" style="width: 450px"  v-if="ruleForm.nature !== '个人'">
           <el-input v-model="ruleForm.nature1" size="mini" placeholder="请输入团队"></el-input>
         </el-form-item>

         <el-form-item>
          <el-button type="primary" @click="next('ruleForm')" style="width: 250px;background-color: #f76c1a;border: #f76c1a">下一步</el-button>
        </el-form-item>

      </el-form>

      <Preliminary2 :val="this.$store.state.preliminary" v-show="this.$store.state.flagTwo"></Preliminary2>
      <Preliminary3 :val="this.$store.state.preliminary" v-if="this.$store.state.flagThree"></Preliminary3>
    </div>
</template>

<script>
    import message from './message'
    import Preliminary2 from './Preliminary2'
    import Preliminary3 from './Preliminary3'
    import store from '../vuex/store'
    import {setCookie,delCookie,getCookie} from '../utils/cookie'

    export default {
        name: "preliminary",
        data() {
          var checkAge = (rule, value, callback) => {
            if (!/^[\u4E00-\u9FA5A-Za-z]{1,6}$/.test(value)) {
                return callback(new Error('请输入正确的姓名'));
            } else {
              callback()
            }
          };
          var checkcall = (rule, value, callback) => {
            if (!value) {
              return callback(new Error('手机不能为空'));
            }else if(!/^[1][3,4,5,7,8][0-9]{9}$/.test(value)){
              return callback(new Error('手机号码格式不正确'));
            }else{
              callback()
            }
          }
          return {
            ruleForm: {
              name: '',  //姓名
              sex:'男',  // 性别
              region: '',  //出生日期
              call: '',  //联系电话
              professional:'', // 职称
              grade:'',   //授课年级
              school:'',  // 任职学校
              address:'',  // 联系地址
              nature:'个人',  // 参赛性质
              nature1:'',  // 团体值
            },
            rules: {
              name: [
                {required: true, message: '请输入姓名', trigger: 'blur'},
                {validator:checkAge ,trigger: 'blur'}
              ],
              sex: [
                { required: true, message: '请选择性别', trigger: 'change' }
              ],
              region: [
                {required: true, message: '请选择日期', trigger: 'blur'}
              ],
              call: [
                {required: true, message: '请输入电话号码', trigger: 'blur'},
                {validator:checkcall ,trigger: 'blur'}
              ],
              grade: [
                {required: true, message: '请输入年级', trigger: 'blur'}
              ],
              professional : [
                {required: true, message: '请输入职称', trigger: 'blur'}
              ],
              school : [
                {required: true, message: '请输入学校名称', trigger: 'blur'}
              ],
              address : [
                {required: true, message: '请输入地址', trigger: 'blur'}
              ],
              nature : [
                {required: true, message: '请输入名称', trigger: 'blur'}
              ],
              nature1 : [
                {required: true, message: '请输入团队名称', trigger: 'blur'}
              ]
            },
            hided:false
          };
        },
        methods: {
          change(val){
            console.log(val, 'val');
            console.log(this.inputValue, 'input的值');
          },
          next(formName){
            // this.$refs[formName].validate((valid) => {
            //   if (valid) {
            //     this.$store.state.flagOne = false
            //     this.$store.state.flagTwo = true
            //     this.$store.state.flagThree = false
            //   } else {
            //     return false;
            //   }
            // });

            this.$store.state.preliminary.preliminary1 = this.ruleForm;
            this.$store.state.flagOne = false
            this.$store.state.flagTwo = true
            this.$store.state.flagThree = false

            if (getCookie('u_uuid')) {
              this.$store.state.preliminary.preliminary1.id = getCookie('u_uuid')
            }

            var obj = {
              id:this.$store.state.preliminary.preliminary1.id,
              actualname:this.$store.state.preliminary.preliminary1.name,
              sex:this.$store.state.preliminary.preliminary1.sex,
              birthdate:this.$store.state.preliminary.preliminary1.region,
              mobile:this.$store.state.preliminary.preliminary1.call,
              jobtitle:this.$store.state.preliminary.preliminary1.professional,
              teachgrade:this.$store.state.preliminary.preliminary1.grade,
              school:this.$store.state.preliminary.preliminary1.school,
              address:this.$store.state.preliminary.preliminary1.address,
              cstype:this.$store.state.preliminary.preliminary1.nature,
            }

            if(obj.cstype == ''){
              obj.cstype = this.$store.state.preliminary.preliminary1.nature1
            }
            if(obj.sex == '男'){
              obj.sex = 0
            }
            if(obj.sex == '女'){
              obj.sex = 1
            }
            this.$post('/levy/preliminary/plyupdate',obj).then(res => {

            })

          }
        },
        components:{
          message,
          Preliminary2,
          Preliminary3
        },
        store
    }
</script>

<style scoped lang="scss">
  .preliminary{
    padding:  28px;
    background-color: #fff;
    span{
      font-size: 18px;
    }
    form{
      margin-top: 40px;
      .nature {
        .el-form-item__content {
          .el-form-item__error{
            margin-left: 20px;
          }
        }
      }
    }

  }
</style>



