<template>
    <div>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

        <el-form-item label="课件名称"  style="width: 450px">
          <!--<el-input v-model="ruleForm.coursewareName" size="mini" placeholder="请输入课件名称"></el-input>-->
          <span>[初赛课件]{{ val.preliminary1.school }}-{{val.preliminary1.name}}-{{call}}</span>
        </el-form-item>


        <el-form-item label="教学设计" prop="teaching">
          <el-upload
            prop="logo"
            :headers="myHeaders"
            ref="upload"
            :show-file-list="true"
            class="upload-demo"
            drag
            :limit="1"
            :on-exceed="handleExceed"
            :before-remove="beforeRemove"
            :onError="uploadError"
            :onSuccess="uploadSuccess"
            :beforeUpload="beforeAvatarUpload"
            action="http://118.31.112.11/levy/csupload/index">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip" style="position: absolute;top: 35px;left: 215px;color: #ccc">格式为word或PDF，且不超过10M</div>
          </el-upload>
        </el-form-item>

        <el-form-item label="演示课件" prop="demonstration">
          <el-upload
            :show-file-list="true"
            class="upload-demo"
            drag
            :headers="myHeaders"
            required
            ref="upload"
            :onError="uploadError"
            :limit="1"
            :on-exceed="handleExceed"
            :before-remove="beforeRemove"
            :onSuccess="uploadSuccess1"
            :beforeUpload="beforeAvatarUpload1"
            action="http://118.31.112.11/levy/csupload/index">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip" style="position: absolute;top: 35px;left: 215px;color: #ccc">格式为PPT，且不超过10M</div>
          </el-upload>
        </el-form-item>

        <el-form-item label="评测练习" prop="evaluating">
          <el-upload
            :show-file-list="true"
            class="upload-demo"
            drag
            required
            :headers="myHeaders"
            :onError="uploadError"
            :limit="1"
            :on-exceed="handleExceed"
            :before-remove="beforeRemove"
            :onSuccess="uploadSuccess2"
            :beforeUpload="beforeAvatarUpload"
            action="http://118.31.112.11/levy/csupload/index">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip" style="position: absolute;top: 35px;left: 215px;color: #ccc">格式为word或PDF，且不超过10M</div>
          </el-upload>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="previous" style="width: 150px;background-color: #f76c1a;border: #f76c1a" >上一步</el-button>
          <el-button type="primary" @click="submit('ruleForm')" style="width: 150px;background-color: #f76c1a;border: #f76c1a" >提交</el-button>
        </el-form-item>

      </el-form>
    </div>
</template>

<script>

    import {setCookie,delCookie,getCookie} from '../utils/cookie'
    import store from '../vuex/store'
    var token =  getCookie('session') // 要保证取到
    export default {
        name: "preliminary2",
        data() {
          var checkAge = (rule, value, callback) => {
            if (!/^[\u4E00-\u9FA5A-Za-z]+$/.test(value)) {
              return callback(new Error('请输入正确的姓名'));
            }else{
              callback()
            }
        };
          return {
            upload1:'',
            upload2:'',
            call:'',
            myHeaders: {Accept: token},
            urlAddress:{},
            coursewareName:'',
            uploaddata:{},  // 获取三个文件名称
            ruleForm: {
              coursewareName: ''//课件名称
            },
            rules: {
              coursewareName: [
                {required: true, message: '请输入课件', trigger: 'blur'},
              ]
            }
         };
        },
        props:{
         val:{
           type:Object
         }
        },
        methods:{
          previous(){
            this.$store.state.flagOne = true
            this.$store.state.flagTwo = false
          },
          submit(formName){
            // let p = new Promise(function(resolve,reject){
            //
            // }).then(function(){
            //   // this.$store.state.flagTwo = false
            //   // this.$store.state.flagThree = true
            //   // this.$refs.upload.submit();
            // }).catch(function(){
            //   console.log(3);
            // })


            // if(this.upload1!=true) {
            //   this.$message({
            //     showClose: true,
            //     message: '请上传教学设计',
            //     type: 'error'
            //   });
            // }else if(this.upload2!=true){
            //   this.$message({
            //     showClose: true,
            //     message: '请上传演示课件',
            //     type: 'error'
            //   });
            // }
            this.$refs[formName].validate((valid) => {
              if (valid) {
                  this.$store.state.flagTwo = false
                  this.$store.state.flagThree = true
                   console.log(this.$store.state.id);
                var obj = {
                    id:this.$store.state.id,
                    csname:this.val.preliminary1.school+'-'+this.val.preliminary1.name+'-'+this.call,
                    designname:this.urlAddress.urlAddress1,
                    demoname:this.urlAddress.urlAddress2,
                    evaluatename:this.urlAddress.urlAddress3
                  }
                  // obj.desiginname = aa;
                  // obj.demoname = aa;
                  // obj.evaluatename = aa;
                  this.$post('/levy/preliminary/apply',obj).then(res => {
                    console.log(res);
                  })

              } else {
                return false;
              }
            });
            this.uploaddata.coursewareName = this.ruleForm.coursewareName; //课件名称赋值
            this.$store.state.preliminary.preliminary2 = this.uploaddata;



          },
          handleExceed(files, fileList){
            this.$message.warning(`只能上传一个文件，您已上传`);
          },
          beforeRemove(file, fileList){

            return this.$confirm(`不符合上传类型，确定移除 ${ file.name }？`);
          },
          uploadError(){

            this.$message({
              showClose: true,
              message: '上传失败，请重试！',
              type: 'error'
            });
          },
          uploadSuccess(response, file, fileList){
            debugger
            this.urlAddress.urlAddress1 = response
            this.$message({
              showClose: true,
              message: '教学上传成功',
              type: 'success'
            });
            this.uploaddata.fileOne = file.name

            if(file.status == 'success'){
              this.upload1=true
            }
          },
          beforeAvatarUpload(file){
            const extension = file.name.split('.')[1] === 'xls'
            const extension2 = file.name.split('.')[1] === 'xlsx'
            const extension3 = file.name.split('.')[1] === 'doc'
            const extension4 = file.name.split('.')[1] === 'docx'
            const extension5 = file.name.split('.')[1] === 'pdf'
            const isLt2M = file.size / 1024 / 1024 < 10
            if (!extension && !extension2 && !extension3 && !extension4 && !extension5) {
              this.$message({
                showClose: true,
                message: '上传模板只能是 world或pdf 格式!',
                type: 'error'
              });
            }
            if (!isLt2M) {
              this.$message({
                showClose: true,
                message: '上传模板大小不能超过 10MB!',
                type: 'error'
              })
            }

           //  let fd = new FormData()
           //  fd.append('file', file)
           //  fd.append('groupId', this.groupId)
           //         // console.log(fd)
           // newVideo(fd).then(res => {
           //    console.log(res)
           //  })
           // return true

            return extension || extension2 || extension3 || extension4 || extension5 && isLt2M
          },
          uploadSuccess1(response, file, fileList){
            this.urlAddress.urlAddress2 = response
            this.$message({
              showClose: true,
              message: '课件上传成功',
              type: 'success'
            });

            console.log(file.name);
            this.uploaddata.fileTwo = file.name
            if (file.status == 'success') {
              this.upload2=true
            }
          },
          beforeAvatarUpload1(file){
            const extension = file.name.split('.')[1] === 'ppt'
            const extension2 = file.name.split('.')[1] === 'pptx'
            const isLt2M = file.size / 1024 / 1024 < 10
            if (!extension && !extension2) {
              this.$message({
                showClose: true,
                message: '上传模板只能是PPT格式!',
                type: 'error'
              });
            }
            if (!isLt2M) {
              this.$message({
                showClose: true,
                message: '上传模板大小不能超过 10MB!',
                type: 'error'
              })
            }
            return extension || extension2 && isLt2M
          },
          uploadSuccess2(response, file, fileList){
            this.urlAddress.urlAddress3 = response
            this.uploaddata.fileThree = file.name
            this.$message({
              showClose: true,
              message: '练习上传成功',
              type: 'success'
            });
          },
        },
        updated(){
          this.call = this.val.preliminary1.call.slice(-4)
        },
       store
    }
</script>

<style scoped lang="scss">
    form{
      margin-top: 40px;
    }
</style>














